import request from '../utils/request'

const api = {
  login: {
    login: (query) => {
      return request({
        url: '/login/login',
        method: 'post',
        data: query
      })
    },
    logout: (query) => {
      return request({
        url: '/login/logout',
        method: 'get',
        params: query
      })
    },
    isLogin:(query)=>{
      return request({
        url: '/login/islogin',
        method: 'get',
        params: query
      })
    }
  },
  video: {
    list: (query) => {
      return request({
        url: '/video/getList',
        method: 'get',
        params: query,
      })
    }, delete: (query) => {
      return request({
        url: '/video/deleteVideo',
        method: 'get',
        params: query,
      })
    },
    update: (query) => {
      return request({
        url: '/video/updateVideo',
        method: 'post',
        data: query,
      })
    }
  },
  user: {

    list: (query) => {
      return request({
        url: '/user/getList',
        method: 'get',
        params: query,
      })
    },
    delete: (query) => {
      return request({
        url: '/user/deleteUser',
        method: 'get',
        params: query,
      })
    },
    update: (query) => {
      return request({
        url: '/user/updateUser',
        method: 'get',
        params: query,
      })
    },
    log: (query) => {
      return request({
        url: '/user/userLog',
        method: 'get',
        params: query,
      })
    },
  },
  type: {
    list: (query) => {
      return request({
        url: '/type/getList',
        method: 'get',
        params: query
      })
    }
  },
  config: {
    get: (query) => {
      return request({
        url: '/config/getConfig',
        method: 'get',
        params: query
      })
    },
    set: (query) => {
      return request({
        url: '/config/setConfig',
        method: 'post',
        data: query
      })
    }
  },
  admin: {
    list: (query) => {
      return request({
        url: '/manager/getList',
        method: 'get',
        data: query
      })
    },
    add: (query) => {
      return request({
        url: '/manager/addUser',
        method: 'post',
        data: query
      })
    },
    update: (query) => {
      return request({
        url: '/manager/updateUser',
        method: 'post',
        data: query
      })
    },
    delete: (query) => {
      return request({
        url: '/manager/deleteUser',
        method: 'post',
        data: query
      })
    },
    log: (query) => {
      return request({
        url: '/manager/log',
        method: 'get',
        data: query
      })
    }
  }
}
export default api



