const devUrl = "http://douyin.com/";
const serverUrl = "http://video.youyacao.com/";
const baseURL = process.env.NODE_ENV === 'development' ? devUrl : serverUrl
const utils = {
	formatUrl:(url)=>{
		if (url == undefined) {
			return;
		  }
		  var index = url.indexOf("http");
		  if (index == 0) {
			return url;
		  }
		  return baseURL + url;
	}
}
export default utils;



