import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    user:null
  },
  mutations: {
    setUser (state, user) {
      uni.setStorageSync('user', user)
      state.user = user
    },
  },
  actions: {
    isLogin(state,vue){
      return new Promise(((resolve, reject) => {
        var res =vue.$api.login.isLogin().then((res)=>{
          resolve(res);
        })
      }))
    }
  },
  modules: {}
})
export default store



