<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
    export default {
        name: 'App',
        mounted () {
            this.$store.dispatch('isLogin',this).then((res)=>{
                if(res.code==1){
                    this.$router.replace({ name: 'login'})
                }
            })
        }
    }
</script>

<style>
  @import "./assets/css/main.css";
  @import "./assets/css/color-dark.css"; /*深色主题*/
</style>



